	export default {
		// 
		components: {

	  	},
		//
		data() {
			return {
				arr: [],
				mess:'',
				labelPosition: '',
				commonList: [ '', ''],
      			popShow: false,				//
      			isSelect1: false,			//
      			isSelect2:false,			//
      			
			}
		},
		// 
		mounted: function() {
			console.log(this.$route.query)
		},
		//
		methods: {

		    // 
		    shouJianRen: function() {
		    	this.popShow = !this.popShow;
		    },
		    // 
		    goMailList: function() {
		    	this.$router.push({
					path: '/mailList',
					name: 'mailList',
					query: {
						'name': 'faduanxin'
					}
				})
		    },
		    // 
		    isSelects1: function() {
		    	this.isSelect1 = !this.isSelect1;
		    	if(this.isSelect1) {
		    		this.isSelect2 = false;
		    	}
		    },
		    // 
		    isSelects2: function() {
		    	this.isSelect2 = !this.isSelect2;
		    	if(this.isSelect2) {
		    		this.isSelect1 = false;
		    	}
		    }
		    
		}
		
	}



