import Vscroll from "../../components/index"
export default {
    components: {
        Vscroll,
    },
    data() {
        
        return {
            counter: 1, //
            num: 10, // 
            pageStart: 0, // 
            pageEnd: 0, // 
            listdata: [], // 
            downdata: [], // 
            scrollData:{
                noFlag: false //
            }
        }
    },
    mounted: function() {
        this.getList();
    },
    methods: {
        //
        getList() {
            var response = []
            for(let i = 0; i < 60; i++) {
                response.push({
                    name: i
                })
            }
            this.listdata = response.slice(0, this.num);
        },
        //
        onRefresh(done) {
            this.getList();
            done(); // call done
        },
        //
        onInfinite(done) {
            this.counter++;
            let end = this.pageEnd = this.num * this.counter;
            let i = this.pageStart = this.pageEnd - this.num;
            let more = this.$el.querySelector('.load-more')
            debugger;
            for(i; i < end; i++) {
                if(i >= 30) {
                    more.style.display = 'none'; //
                    //
                    this.scrollData.noFlag = true;
                    break;
                } else {
                    this.downdata.push({
                        name: i + "==="
                    })
                    more.style.display = 'none'; //
                }
            }
            debugger;
            done();
        }
    }
}



